import React from 'react';

const Dashboard = React.lazy(() => import('./views/Dashboard'));
const Users = React.lazy(() => import('./views/Users/Users'));
const User = React.lazy(() => import('./views/Users/User'));
const QuestionList = React.lazy(() => import('./views/Forum/QuestionList'));
const CreateQuestion = React.lazy(() => import('./views/Forum/CreateQuestion'));
const Question = React.lazy(() => import('./views/Forum/Question'));
const About = React.lazy(() => import('./views/About/About'));
const Values = React.lazy(() => import('./views/About/Values/Values'));
const Financial = React.lazy(() => import('./views/Survey/Financial'));
const Survey = React.lazy(() => import('./views/Survey/Survey'));
const ChatList = React.lazy(() => import('./views/Survey/ChatList'));
const SurveyList = React.lazy(() => import('./views/Survey/SurveyList'));
const OrgPortal = React.lazy(() => import('./views/Careers/Portal'));

// https://github.com/ReactTraining/react-router/tree/master/packages/react-router-config
const routes = [
  { path: '/', exact: true, name: 'Home' },
  { path: '/dashboard', name: 'Dashboard', component: Dashboard },
  { path: '/users', exact: true,  name: 'Users', component: Users },
  { path: '/users/:id', exact: true, name: 'User Details', component: User },
  { path: '/forum/question-list', exact: true, name: 'Question List', component: QuestionList },
  { path: '/forum/create-question', exact: true, name: 'Create Question', component: CreateQuestion },
  { path: '/forum/question/:questionId', exact: true, name: 'Question', component: Question },
  { path: '/about', exact: true, name: 'About', component: About },
  { path: '/values', exact: true, name: 'Values', component: Values },
  { path: '/survey/financial', exact: true, name: 'Financial Opportunities', component: Financial },
  { path: '/survey/survey', exact: true, name: 'Online Survey', component: Survey },
  { path: '/survey/chatlist', exact: true, name: 'Chat List', component: ChatList },
  { path: '/survey/surveylist', exact: true, name: 'Survey List', component: SurveyList },
  { path: '/careers/portal', exact: true, name: 'Support Portal', component: OrgPortal },
];

export default routes;
