import React, { Component, Suspense } from 'react';
import { Redirect, Route, Switch } from 'react-router-dom';
import * as router from 'react-router-dom';
import { Container, Modal, ModalBody, Button, ModalHeader, ModalFooter, Input } from 'reactstrap';

import {
  AppAside,
  AppFooter,
  AppHeader,
  AppSidebar,
  AppSidebarFooter,
  AppSidebarForm,
  AppSidebarHeader,
  AppSidebarMinimizer,
  AppBreadcrumb2 as AppBreadcrumb,
  AppSidebarNav2 as AppSidebarNav,
} from '@coreui/react';
// sidebar nav config
import navigation from '../../_nav';
// routes config
import routes from '../../routes';
import Cookies from 'universal-cookie';
const cookies = new Cookies();

const DefaultAside = React.lazy(() => import('./DefaultAside'));
const DefaultHeader = React.lazy(() => import('./DefaultHeader'));
const DefaultChatBot = React.lazy(() => import('./DefaultChatBot'));

class DefaultLayout extends Component {
  constructor(props) {
    super(props);

    this.state = {
      modalOpen: false,
      isAdmin: false,
    };
    this.toggleModal = this.toggleModal.bind(this);
  }

  toggleModal() {
    this.setState({
      modalOpen: !this.state.modalOpen,
    });
  }

  loading = () => <div className="animated fadeIn pt-1 text-center">Loading...</div>

  signOut(e) {
    e.preventDefault()
    cookies.remove('token', { path: '/' });
    cookies.remove('admin', { path: '/' });
    this.props.history.push('/login')
  }

  componentWillMount(){
    if (cookies.get('admin') !== 'false'){
      this.setState({
        isAdmin: true
      });
    } else {
      this.setState({
        isAdmin: false
      });
    }
  }

  render() {
    return (
      <div className="app">
        <AppHeader fixed>
          <Suspense fallback={this.loading()}>
            <DefaultHeader onLogout={e => this.signOut(e)} />
          </Suspense>
        </AppHeader>
        <div className="app-body full-body">
          {/* <AppSidebar fixed display="lg">
            <AppSidebarHeader />
            <AppSidebarForm />
            <Suspense>
              <AppSidebarNav navConfig={this.state.isAdmin ? navigation.admin_list : navigation.user_list} {...this.props} router={router} />
            </Suspense>
            <AppSidebarFooter />
            <a onClick={this.toggleModal} style={{cursor: 'pointer'}}><div style={{ textAlign: 'center', height: '30px' }}>Emergency Contact</div></a>
            <AppSidebarMinimizer />
          </AppSidebar> */}
          <DefaultChatBot />
          <main className="main">
            <AppBreadcrumb appRoutes={routes} router={router} />
            <Container fluid>
              <Suspense fallback={this.loading()}>
                <Switch>
                  {routes.map((route, idx) => {
                    return route.component ? (
                      <Route
                        key={idx}
                        path={route.path}
                        exact={route.exact}
                        name={route.name}
                        render={props => (
                          <route.component {...props} />
                        )} />
                    ) : (null);
                  })}
                  <Redirect from="/" to="/dashboard" />
                </Switch>
              </Suspense>
            </Container>
          </main>
          <AppAside fixed>
            <Suspense fallback={this.loading()}>
              <DefaultAside />
            </Suspense>
          </AppAside>
        </div>
        <Modal isOpen={this.state.modalOpen} toggle={this.toggleModal}
          className={'modal-primary ' + this.props.className}>
          <ModalHeader>Emergency Contact</ModalHeader>
          <ModalBody>
            <table style={{width: '100%'}}>
              <tbody>
                <tr>
                  <td><b>Ambulance/ Fire/ Police</b></td>
                  <td></td>
                  <td><a href="tel:000">000</a></td>
                </tr>
                <tr>
                  <td>&nbsp;</td>
                </tr>
                <tr>
                  <td><b>Lifeline,24hr counselling</b></td>
                  <td><a href="http://www.lifeline.org.au" target="_blank">www.lifeline.org.au</a></td>
                  <td><a href="tel:131114">131114</a></td>
                </tr>
                <tr>
                  <td>&nbsp;</td>
                </tr>
                <tr>
                  <td><b>Suicide Call Back Service</b></td>
                  <td><a href="http://www.suicidecallbackservice.org.au" target="_blank">www.suicidecallbackservice.org.au</a></td>
                  <td style={{width: '100px'}}><a href="tel:1300 659 467">1300 659 467</a></td>
                </tr>
                <tr>
                  <td>&nbsp;</td>
                </tr>
                <tr>
                  <td><b>1800 RESPECT (Domestic Violence   Support)</b></td>
                  <td><a href="http://www.1800respect.org.au" target="_blank">www.1800respect.org.au</a></td>
                  <td><a href="tel:1800 737 732">1800 737 732</a></td>
                </tr>
              </tbody>
            </table>
          </ModalBody>
        </Modal>
      </div>
    );
  }
}

export default DefaultLayout;
